  font-family: 'Circe-Bold';
  src: local("Circe Bold"), local("Circe-Bold"), url("../fonts/Circe/Circe-Bold.woff2") format("woff2"), url("../fonts/Circe/Circe-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  color: #313741;
  font-family: 'Circe-Regular', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  min-width: 320px;
  background-color: #fff;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

@media (max-width: 1200px) {
  .container {
    max-width: 1160px;
    padding: 0 20px;
  }
}

@media (max-width: 730px) {
  .container {
    max-width: 1170px;
    padding: 0 15px;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0;
}

img {
  max-width: 100%;
}

svg {
  width: 100%;
  height: 100%;
}

input, button {
  border: none;
  outline: none;
}

button {
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.title {
  font-family: 'Circe-Bold', sans-serif;
  font-weight: 700;
  font-size: 45px;
}

@media (max-width: 768px) {
  .title {
    font-size: 28px;
  }
}

.modal {
  --transition-time: 0.3s;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.5);
  cursor: pointer;
  overflow-y: auto;
  overflow-x: hidden;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  transition: opacity var(--transition-time), visibility var(--transition-time);
}

.modal::before {
  content: '';
  display: inline-block;
  vertical-align: middle;
  height: 100%;
}

.modal__container {
  position: relative;
  width: 1000px;
  margin: 50px auto;
  display: none;
  vertical-align: middle;
  background-color: #fff;
  border-radius: 20px;
  cursor: default;
  text-align: left;
}

@media (max-width: 1024px) {
  .modal__container {
    width: 90%;
  }
}

.modal.is-open {
  opacity: 1;
  visibility: visible;
  transition: opacity var(--transition-time), visibility var(--transition-time);
}

.modal__container.modal-open {
  display: inline-block;
}

.fade {
  opacity: 0;
  transition: opacity var(--transition-time);
}

.fade.animate-open {
  opacity: 1;
  transition: opacity var(--transition-time);
}

.fadeInUp {
  opacity: 0;
  transform: translateY(-100px);
  transition: opacity var(--transition-time), transform var(--transition-time);
}

.fadeInUp.animate-open {
  opacity: 1;
  transform: translateY(0);
  transition: opacity var(--transition-time), transform var(--transition-time);
}

*:focus {
  outline: 1px solid #ffbd46;
}

.content {
  min-height: 200vh;
}

.disable-scroll {
  position: relative;
  overflow: hidden;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
}

.modal__container {
  position: relative;
  max-width: 630px;
  text-align: center;
  padding: 30px;
  border-radius: 0;
}

.modal-close {
  position: absolute;
  right: 4%;
  top: 4%;
  display: block;
  max-width: 14px;
  max-height: 14px;
  margin-left: auto;
}

.modal-close svg {
  display: inline-block;
  fill: currentColor;
  max-width: 14px;
  max-height: 14px;
  overflow: visible;
}

.modal-title {
  font-family: 'Circe-Bold', sans-serif;
  font-weight: 700;
  font-size: 35px;
  margin-bottom: 15px;
}

@media (max-width: 478px) {
  .modal-title {
    font-size: 28px;
  }
}

.modal-descr {
  max-width: 393px;
  margin: 0 auto;
  margin-bottom: 27px;
  font-size: 18px;
  color: #6f7784;
}

@media (max-width: 478px) {
  .modal-descr {
    font-size: 16px;
  }
}

.modal-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 368px;
  margin: 0 auto;
}

.modal-form .js-validate-error-label {
  margin-top: -23px;
  margin-bottom: 10px;
}

.modal-form input {
  width: 100%;
  padding: 10px 20px;
  margin-bottom: 27px;
  font-size: 18px;
  border: 1px solid #d5dce5;
  border-radius: 100px;
}

.modal-btn {
  width: 100%;
  padding: 10px 20px;
  background-color: #2694f5;
  padding: 15px 37px;
  font-family: 'Circe-Bold', sans-serif;
  font-weight: 700;
  color: #fff;
  border-radius: 50px;
  transition: background-color .3s ease;
}

